.modal {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	background: rgba(0, 0, 0, .9);

	&__window {
		position: relative;
		width: 100%;
		max-width: 60rem;
		padding: 2rem 2rem 3rem;
		background: #fff; }

	&__close {
		position: absolute;
		top: .75rem;
		right: .75rem;
		width: 1rem;
		height: 1rem;
		background-image: url('data:image/svg+xml;base64,');
		background-size: cover;
		cursor: pointer; }

	&__title {
		padding-bottom: 2rem;
		font-size: 2rem;
		font-weight: 700; } }
